import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {Language} from 'src/app/models/language';
import {SupportedLanguages} from 'src/app/config/localization';

@Component({
  selector: 'app-language-selector',
  templateUrl: './language-selector.component.html',
  styleUrls: ['./language-selector.component.scss']
})
export class LanguageSelectorComponent implements OnInit {


  langs: Language[] = SupportedLanguages;
  @Input() default : string = "en";
  public selectedLanguage: Language = null;

  @Output() languageChanged : EventEmitter<Language>  = new EventEmitter();
  constructor() {
  }

  changeSelect(lang: Language) {
    this.selectedLanguage = lang;
    this.languageChanged.emit(lang);
  }
  ngOnInit() {
    this.changeSelect(this.langs[0]);
  }

}
