import {Component, Input, OnInit, ViewChild, ViewEncapsulation} from '@angular/core';
import {TextAction, TextActionValue} from 'src/app/models/book';
import {MatDialog} from '@angular/material';
import {TextActionEditDialogComponent} from 'src/app/site/dashboard/page/actions/text-action-edit-dialog/text-action-edit-dialog.component';
import {LanguageSelectorComponent} from 'src/app/site/dashboard/language/language-selector/language-selector.component';

@Component({
  selector: 'app-text-action',
  templateUrl: './text-action.component.html',
  styleUrls: ['./text-action.component.scss'] ,
  encapsulation : ViewEncapsulation.None
})
export class TextActionComponent implements OnInit {

  @Input() action: TextAction;
  @ViewChild(LanguageSelectorComponent) languageSelector;
  get selectedLanguageKey(): string {
    return this.languageSelector.selectedLanguage.value;
  }
  constructor(private dialog: MatDialog) {
  }



  ngOnInit() {


  }

  openEditDialog() {

    if (!this.action.value[this.selectedLanguageKey]) {
      this.action.value[this.selectedLanguageKey] = new TextActionValue();
    }

    const dialogRef = this.dialog.open(TextActionEditDialogComponent, {
      width: '600px',
      height: '500px',
      data: this.action.value[this.selectedLanguageKey].text
    });

    dialogRef.afterClosed().subscribe(result => {
      if(result === null) return ;
      this.action.value[this.selectedLanguageKey].text = result;
    });
  }


}
