import {Component, Inject, OnInit} from '@angular/core';
import {MAT_DIALOG_DATA, MatDialogRef} from '@angular/material';
import {Page} from 'src/app/models/book';
import {Language} from 'src/app/models/language';
import {environment} from 'src/environments/environment';

@Component({
  selector: 'app-add-page-dialog',
  templateUrl: './add-page-dialog.component.html',
  styleUrls: ['./add-page-dialog.component.scss']
})
export class AddPageDialogComponent implements OnInit {


  apiEndPoint = environment.apiEndPoint;
  page: Page = new Page();
  selectedLanguage: string;

  constructor(public dialogRef: MatDialogRef<AddPageDialogComponent>,
              @Inject(MAT_DIALOG_DATA) public data: Page) {

    if (data) {
      this.page = data;
    }
  }

  ngOnInit() {


    if (!this.page.id) {
      this.page.id = Page.generateId();
    }
  }

  languageChanged(language: Language) {
    this.selectedLanguage = language.value;
  }

  close() {
    this.dialogRef.close(this.page);
  }

}
