import {Component, Inject, OnInit} from '@angular/core';
import {MAT_DIALOG_DATA, MatDialogRef} from '@angular/material';
import {FormControl} from '@angular/forms';

@Component({
  selector: 'app-text-action-edit-dialog',
  templateUrl: './text-action-edit-dialog.component.html',
  styleUrls: ['./text-action-edit-dialog.component.scss']
})
export class TextActionEditDialogComponent implements OnInit {

  constructor( public dialogRef: MatDialogRef<TextActionEditDialogComponent>,
               @Inject(MAT_DIALOG_DATA) public data : string) { }
  textFormControl : FormControl  = new FormControl();
  ngOnInit() {
    this.textFormControl.setValue(this.data);
  }

  editAndClose(){
    this.dialogRef.close(this.textFormControl.value);
  }

}
