import {Injectable} from '@angular/core';
import {UploadState, UploadxOptions, UploadxService} from 'ngx-uploadx';
import {Observable, Subject} from 'rxjs';
import {tap} from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class UploadService {

  fileCompleted: Subject<UploadState> = new Subject();
  private uploadxOptions: UploadxOptions = {
    concurrency: 2,
    allowedTypes: 'image/*,video/*',
    endpoint: `/uploadx?uploadType=uploadx`,
    token: () => {
      return localStorage.getItem('access_token');
    },
    headers: f => {
      return {
        // upload additional data with the uploaded file
        //@ts-ignore
        'bookId': f.bookId
      };
    },
    autoUpload: true,
    chunkSize: 1024 * 256 * 8
  };

  constructor(private ngxUpload: UploadxService) {
    this._state = this.ngxUpload.init(this.uploadxOptions);
    this._state = this._state.pipe(tap((state: UploadState) => {
      if (state.status == 'complete') {
        console.log('completed');
        this.fileCompleted.next(state);
      }
    }));
  }

  private _state: Observable<UploadState>;

  get state(): Observable<UploadState> {
    return this._state;
  }

  queue(file: File) {
    this.ngxUpload.handleFile(file);
  }

  uploadAll() {
    this.ngxUpload.control({action: 'uploadAll'});

  }

  pause(file: UploadState) {
    this.ngxUpload.control({action: 'pause', uploadId: file.uploadId});
  }

  resume(file: UploadState) {
    this.ngxUpload.control({action: 'upload', uploadId: file.uploadId});
  }

  cancel(file: UploadState) {
    this.ngxUpload.control({action: 'cancel', uploadId: file.uploadId});
  }
}
