import {Component, Input, OnInit, ViewEncapsulation} from '@angular/core';
import {Action, Book, Shape} from 'src/app/models/book';

@Component({
  selector: 'app-shape',
  templateUrl: './shape.component.html',
  styleUrls: ['./shape.component.scss'] ,
  encapsulation: ViewEncapsulation.None
})
export class ShapeComponent implements OnInit {

  @Input() book : Book ;
  @Input() shape : Shape = null;
  constructor() { }
  ngOnInit() {}
  // used to show actions even if the shape is null
  get actions() : Action[] {

    if(!this.shape) return [];
    return this.shape.actions;
  }

  actionCreated(action : Action){

    //@todo disalbe addAction when shape is null
    if(!this.shape) {

      return ;
    }
    this.shape.actions.push(action);
  }

  remove(action : Action){

    this.shape.actions = this.actions.filter( ac => ac != action);
  }

}
