import {Component, Input, OnInit, ViewChild} from '@angular/core';
import {FilePickerDirective, ReadFile, ReadMode} from 'ngx-file-helpers';
import {UploadService} from 'src/app/services/upload.service';

@Component({
  selector: 'app-upload-button',
  templateUrl: './upload-button.component.html',
  styleUrls: ['./upload-button.component.scss']
})
export class UploadButtonComponent implements OnInit {

  @ViewChild(FilePickerDirective)
  private filePicker: FilePickerDirective;

  readMode : ReadMode = ReadMode.arrayBuffer;


  @Input() bookId : string = "" ;
  constructor(private uploadService : UploadService) { }

  ngOnInit() {
  }

  chooseFile(file : ReadFile){


    // @ts-ignore
    file.underlyingFile.bookId = this.bookId ;

    this.uploadService.queue(file.underlyingFile);
    this.uploadService.uploadAll();
    this.filePicker.reset();
  }


}
