import { Injectable } from '@angular/core';
import {ApiHttpClient} from 'src/app/services/api-http-client.service';
import {Observable} from 'rxjs';
import {Media} from 'src/app/models/media';
import {map} from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class MediaService {

  constructor(private http: ApiHttpClient) { }

  get() : Observable<Media[]> {
    return this.http.get("/media").pipe(map(res => {
      return res.data.map((m) =>Media.resolve(m));
    }));
  }
  getBookMedia(bookId : string) : Observable<Media[]> {
    return this.http.get("/media"  , {}/*{ "filter[where][bookId]" : bookId} */ ).pipe(map(res => {
      return res.data.map((m) =>Media.resolve(m));
    }));
  }

  delete(id:string) {
    return this.http.delete(`/media/${id}`);
  }

}
