import {Component, Input, OnInit} from '@angular/core';
import {MatDialog} from '@angular/material';
import {AddPageDialogComponent} from 'src/app/site/dashboard/book/add-page-dialog/add-page-dialog.component';
import {Book, Page} from 'src/app/models/book';
import {ActivatedRoute} from '@angular/router';
import {switchMap} from 'rxjs/operators';
import {BookService} from 'src/app/services/book.service';
import {ToastrService} from 'ngx-toastr';

@Component({
  selector: 'app-book',
  templateUrl: './book.component.html',
  styleUrls: ['./book.component.scss']
})
export class BookComponent implements OnInit {

  @Input() book: Book = null;
  selectedPage: Page;
  disabled: boolean = false;

  constructor(private dialog: MatDialog, private route: ActivatedRoute
    , private bookService: BookService, private toastr: ToastrService) {
  }

  ngOnInit() {


    this.route.paramMap.pipe(switchMap(params => {
      return this.bookService.getById(params.get('id'));
    })).subscribe(book => {
      this.book = book;
    });
  }

  openAddPageDialog() {


    const dialogRef = this.dialog.open(AddPageDialogComponent, {
      width: '600px',

    });
    dialogRef.afterClosed().subscribe((result: Page) => {

      if (!result) {
        return;
      }
      this.book.pages.push(result);

    });

  }

  disable() {
    this.disabled = true;
  }

  enable() {
    this.disabled = false;
  }

  async saveBook() {

    if (this.disabled) {
      return;
    }
    this.disable();
    try {

      await this.bookService.update(this.book).toPromise();
      this.toastr.success('The book has been updated successfully');
    } catch (e) {

      this.toastr.error('An error has been occurred');
    } finally {
      this.enable();
    }
  }
}
