import {Component, OnInit} from '@angular/core';
import {UploadService} from 'src/app/services/upload.service';
import {UploadState} from 'ngx-uploadx';

@Component({
  selector: 'app-file-uploader',
  templateUrl: './file-uploader.component.html',
  styleUrls: ['./file-uploader.component.scss']
})
export class FileUploaderComponent implements OnInit {

  files: { [key: string]: UploadState } = {};

  constructor(private uploadService: UploadService) {
  }

  get fileLoop() {
    return Object.values(this.files);
  }

  ngOnInit() {
    this.uploadService.state.subscribe(this.stateEvent.bind(this));
  }

  stateEvent(state: UploadState) {
    if (['added', 'queued', 'uploading'].includes(state.status)) {
      this.files[state.uploadId] = state;
    }

    if (state.status == 'complete') {
      // upload completed
      this.remove(this.files[state.uploadId]);
    }
  }

  resume(file: UploadState) {
    this.uploadService.resume(file);
  }

  pause(file: UploadState) {
    this.uploadService.pause(file);
  }

  cancel(file: UploadState) {
    this.uploadService.cancel(file);
    this.remove(file);
  }

  remove(file: UploadState) {
    delete this.files[file.uploadId];
  }


}
