import {AfterViewInit, Component, ElementRef, EventEmitter, OnInit, Output, ViewChild, ViewEncapsulation} from '@angular/core';
import * as $ from 'jquery';
import {Shape} from 'src/app/models/book';

@Component({
  selector: 'app-drawing',
  templateUrl: './drawing.component.html',
  styleUrls: ['./drawing.component.scss'] ,
  encapsulation : ViewEncapsulation.None
})
export class DrawingComponent implements OnInit, AfterViewInit {

  @ViewChild('svgWrapper') svgWrapper: ElementRef;
  svgs = ['circle', 'ellipse', 'image', 'path', 'polygon', 'polyline', 'rect', 'text', 'tspan'];
  selectionRect = null;
  selectedShape: string;
  @Output() onSelectionChange: EventEmitter<string> = new EventEmitter();

  constructor() {
  }

  get svgContent() {


    // get svg content without selection rect and without selected-svg class
    let svg = $($(this.svgWrapper.nativeElement).clone());
    svg.find('.selected-svg').removeClass('selected-svg');
    svg.find('.selection-rect').remove();
    return svg.html();
  }

  ngOnInit() {
  }

  changeSvg(content) {

    $(this.svgWrapper.nativeElement).html(content);
    let rect = document.createElementNS('http://www.w3.org/2000/svg', 'rect');
    this.selectionRect = $(rect);
    this.selectionRect.attr({
      x: 10, y: 10, height: 0, width: 0
    });
    this.selectionRect.css({
      fill: 'none',
      stroke: 'rgba(33,33,33,0.6)',
      strokeWidth: 2,
    });
    this.selectionRect.addClass("selection-rect");
    $('svg').append(this.selectionRect);

    this.selectedShape = null;
    this.onSelectionChange.emit(this.selectedShape);
  }


  moveSelectionRect({x ,y , width , height }){
    const padder = 10;
    width += padder;
    height += padder;
    x -= padder / 2;
    y -= padder / 2;

    this.selectionRect.attr({width, height, x, y});

  }

  selectSvg(el) {

    // if selection rect clicked
    if (el == this.selectionRect[0]) {
      return;
    }

    this.moveSelectionRect(el.getBBox());

    if (!el.id) {
      // generate unique id if non existed
      el.id = Shape.generateId();
      //svg content changed


    }

    $('.selected-svg').removeClass('selected-svg');
    $(el).addClass('selected-svg');

    this.selectedShape = el.id;
    this.onSelectionChange.emit(this.selectedShape);

  }

  ngAfterViewInit(): void {

    $('body').on('click', this.svgs.join(','), function ($event) {
      this.selectSvg($event.target);
    }.bind(this));

  }

}
