import {Component, EventEmitter, Input, OnInit} from '@angular/core';
import {MediaService} from 'src/app/services/media.service';
import {Media} from 'src/app/models/media';
import {UploadService} from 'src/app/services/upload.service';

@Component({
  selector: 'app-files',
  templateUrl: './files.component.html',
  styleUrls: ['./files.component.scss']
})
export class FilesComponent implements OnInit {



  @Input() bookId : string = "" ;
  // true for popup dialog
  @Input() dialog : boolean = false;

  constructor(private mediaService  : MediaService , private uploadService : UploadService) { }
  medias : Media[] = [] ;

  ngOnInit() {
    this.load();

    this.uploadService.fileCompleted.subscribe(()=>{
      // refresh everytime you upload a new file
      // @todo add recent file for better performance
      console.log('test');
      this.load();
    });
  }

  async load() {
    this.medias = await this.mediaService.getBookMedia(this.bookId).toPromise();
  }

  mediaEventEmitter : EventEmitter<Media> = new EventEmitter();
  mediaClicked(media : Media){
    this.mediaEventEmitter.emit(media);
  }
}
