import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {Book, Page} from 'src/app/models/book';
import {MatDialog} from '@angular/material';
import {AddPageDialogComponent} from 'src/app/site/dashboard/book/add-page-dialog/add-page-dialog.component';

@Component({
  selector: 'app-book-pages',
  templateUrl: './book-pages.component.html',
  styleUrls: ['./book-pages.component.scss']
})
export class BookPagesComponent implements OnInit {

  constructor(private dialog : MatDialog) { }
  @Input() book : Book ;
  get pages() : Page[]{
    return this.book.pages;
  }
  @Output() pageSelected : EventEmitter<Page> = new EventEmitter();
  selectedPage : Page;

  changePage(page:Page){
    this.selectedPage =  page;
    this.pageSelected.emit(page);
  }
  ngOnInit() {
    if (this.pages.length != 0)
    {
      this.changePage(this.pages[0]);
    }
  }

  openEditDialog(page : Page){



    const dialogRef = this.dialog.open(AddPageDialogComponent, {
      width: '600px',
      data : page
    });
    dialogRef.afterClosed().subscribe((result: Page) => {
      if (!result) {
        return;
      }
    });


  }








}
