import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {DashboardComponent} from 'src/app/site/dashboard/dashboard.component';
import {BookComponent} from 'src/app/site/dashboard/book/book.component';
import {MediaManagerComponent} from 'src/app/site/dashboard/media-manager/media-manager.component';

const routes: Routes = [
  {
    path: 'dashboard', component: DashboardComponent ,

  },
  { path : 'books/:id' , component : BookComponent , pathMatch:'full' } ,
  {path : 'media/:id' , component : MediaManagerComponent} ,
  {path : '**' , redirectTo : 'dashboard'}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule {
}
