import {Injectable} from '@angular/core';
import {ApiHttpClient} from 'src/app/services/api-http-client.service';
import {Book} from 'src/app/models/book';
import {Observable} from 'rxjs';
import {map} from 'rxjs/operators';
import {ApiResponse} from 'src/app/models/api-response';
@Injectable({
  providedIn: 'root'
})
export class BookService {

  constructor(private http: ApiHttpClient) {
  }


  post(book: Book): Observable<Book> {

    return this.http.post('/books', book).pipe(map(response => {
      return Book.resolve(response.data);
    }));
  }

  patch ( book : Partial<Book> ) : Observable<ApiResponse> {
      return this.http.patch(`/books/${book.id}` , book);
  }

  get(): Observable<Book[]> {
    return this.http.get('/books' ,
      {
        "filter[fields][id]" : "true" ,
        "filter[fields][name]" : "true" ,
        "filter[fields][description]" : "true" ,

      }).pipe(map(response => {
      return response.data.map((book) => Book.resolve(book));
    }));
  }

  getById(id: string): Observable<Book> {

    // @todo return only descriptions and names
    return this.http.get(`/books/${id}`).pipe(map(response => {
      return Book.resolve(response.data);
    }));
  }

  update(book: Book): Observable<ApiResponse> {
    return this.http.put(`/books/${book.id}`, book);
  }

  delete(book :Book) : Observable<ApiResponse>{
    return this.http.delete(`/books/${book.id}`);
  }

}
