import {Component, ElementRef, Input, OnInit, ViewChild} from '@angular/core';
import {AudioAction, AudioActionValue} from 'src/app/models/book';
import {FilePickerDirective, ReadMode} from 'ngx-file-helpers';
import {DomSanitizer, SafeUrl} from '@angular/platform-browser';
import {LanguageSelectorComponent} from 'src/app/site/dashboard/language/language-selector/language-selector.component';
import {MatDialog} from '@angular/material';
import {AudioActionEditDialogComponent} from 'src/app/site/dashboard/page/actions/audio-action-edit-dialog/audio-action-edit-dialog.component';
import {Media} from 'src/app/models/media';


@Component({
  selector: 'app-audio-action',
  templateUrl: './audio-action.component.html',
  styleUrls: ['./audio-action.component.scss']
})
export class AudioActionComponent implements OnInit {

  @ViewChild('audio') audioElement: ElementRef;
  readMode: ReadMode = ReadMode.arrayBuffer;
  @Input() action: AudioAction;
  @ViewChild(LanguageSelectorComponent) languageSelector;
  playing: boolean = false;
  source: SafeUrl = null;
  @ViewChild(FilePickerDirective)
  private filePicker: FilePickerDirective;

  constructor(private sanitizer: DomSanitizer, private dialog: MatDialog) {
  }

  get selectedLanguageKey(): string {
    return this.languageSelector.selectedLanguage.value;
  }

  changeSource() {

    let value = this.action.value[this.selectedLanguageKey];
    if (!value || !value.href) {
      this.source = null;
    } else {
      this.source = this.sanitizer.bypassSecurityTrustUrl(value.href);

    }

    this.playing = false;

  }

  languageChanged() {
    this.changeSource();
  }

  ngOnInit() {
  }

  chooseFile($event) {
    if (!this.action.value[this.selectedLanguageKey]) {
      this.action.value[this.selectedLanguageKey] = new AudioActionValue();
    }
    this.action.value[this.selectedLanguageKey].href = URL.createObjectURL($event.underlyingFile);
    this.changeSource();
    this.filePicker.reset();
  }


  chooseMedia() {


    const dialogRef = this.dialog.open(AudioActionEditDialogComponent, {
      width: '80%',
      height: '500px',
    });
    if (!this.action.value[this.selectedLanguageKey]) {
      this.action.value[this.selectedLanguageKey] = new AudioActionValue();
    }

    dialogRef.afterClosed().subscribe(((result: Media) => {
      if (result === null) {
        return;
      }
      this.action.value[this.selectedLanguageKey].mediaId = result.id;
      this.action.value[this.selectedLanguageKey].href = result.url;

      this.changeSource();

    }));

  }

  togglePlay() {

    if (!this.playing) {
      this.audioElement.nativeElement.play();
    } else {
      this.audioElement.nativeElement.pause();
    }
    this.playing = !this.playing;
  }


}
