import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {MatDialog} from '@angular/material';
import {AddActionDialogComponent} from 'src/app/site/dashboard/page/actions/add-action-dialog/add-action-dialog.component';
import {Action, ActionType, AudioAction, NavigationAction, TextAction, UrlAction} from 'src/app/models/book';

@Component({
  selector: 'app-add-action',
  templateUrl: './add-action.component.html',
  styleUrls: ['./add-action.component.scss']
})
export class AddActionComponent implements OnInit {


  @Output() actionCreated: EventEmitter<Action> = new EventEmitter();

  @Input() disabled : boolean = false;
  constructor(public dialog: MatDialog) {
  }

  ngOnInit() {
  }


  resolveAction(type : string) : Action{

    if(type == ActionType.Text){
      return new TextAction();
    }else if(type == ActionType.Audio){
      return new AudioAction();
    }else if (type == ActionType.Url){
      return new UrlAction();
    }else if(type == ActionType.Navigation){
      return new NavigationAction();
    }
    return null;

  }
  openDialog() {

    if(this.disabled) return ; 
    const dialogRef = this.dialog.open(AddActionDialogComponent, {
      width: '250px',
    });

    dialogRef.afterClosed().subscribe(result => {

      if(!result) return ;
      let action:Action = this.resolveAction(result);
      action.type = result;
      this.actionCreated.emit(action);
    });
  }


}
