import {Component, OnInit, ViewChild} from '@angular/core';
import {FilesComponent} from 'src/app/site/dashboard/media-manager/files/files.component';
import {Media} from 'src/app/models/media';
import {MatDialogRef} from '@angular/material';

@Component({
  selector: 'app-audio-action-edit-dialog',
  templateUrl: './audio-action-edit-dialog.component.html',
  styleUrls: ['./audio-action-edit-dialog.component.scss']
})
export class AudioActionEditDialogComponent implements OnInit {


  @ViewChild(FilesComponent) files;

  constructor(public dialogRef: MatDialogRef<AudioActionEditDialogComponent>) {
  }

  mediaSelected(media: Media) {
    this.dialogRef.close(media);
  }

  ngOnInit() {
    this.files.mediaEventEmitter.subscribe(this.mediaSelected.bind(this));
  }

}
