import {Component, Input, OnInit, ViewChild} from '@angular/core';
import {Book, Page, Shape} from 'src/app/models/book';
import {FilePickerDirective, ReadMode} from 'ngx-file-helpers';
import {DrawingComponent} from 'src/app/site/dashboard/page/drawing/drawing.component';

@Component({
  selector: 'app-page',
  templateUrl: './page.component.html',
  styleUrls: ['./page.component.scss']
})
export class PageComponent implements OnInit {

  @ViewChild(DrawingComponent) drawing: DrawingComponent;

  selectedShape: Shape = null;
  readMode: ReadMode = ReadMode.binaryString;
  @ViewChild(FilePickerDirective) private filePicker: FilePickerDirective;
  constructor() {
  }

  _page: Page = null;

  get page(): Page {
    return this._page;
  }

  @Input() book : Book ;
  @Input('page') set page(page: Page) {
    this._page = page;
    this.updateDrawing();
  }

  updateDrawing() {
    this.drawing.changeSvg(this.page.content);
  }

  ngOnInit() {
  }

  chooseFile($event) {
    this.drawingFileChange($event.content);
    this.filePicker.reset();
  }

  drawingFileChange(content: string) {
    this.page.shapes = {};
    this.selectedShape = new Shape();
    this.page.content = content;
    this.updateDrawing();


  }

  drawingSelectionChanged(selectedId: string) {
    if (!selectedId) {
      this.selectedShape = null;
      return;
    }
    if (!this.page.shapes[selectedId]) {
      this.page.shapes[selectedId] = new Shape();
    }
    this.selectedShape = this.page.shapes[selectedId];
    this.page.content = this.drawing.svgContent;
  }


}
