export enum ActionType {
  Text = 'text', Audio = 'audio', Navigation = 'navigation', Url = 'url'
}

export class Action {
  type: ActionType;
  value: any = null;
}

export class TextActionValue {
  text: string = '';
}

export class TextAction extends Action {


  // transable TextActionValue
  value: { [s: string]: TextActionValue } = {};

  constructor() {
    super();
    this.type = ActionType.Text;
  }
}

export class AudioActionValue {
  href: string = '';
  mediaId:string ='' ;
}

export class AudioAction extends Action {

  // transable AudioActionValue
  value: { [s: string]: AudioActionValue } = {};

  constructor() {
    super();
    this.type = ActionType.Audio;
  }
}

export class NavigationAction extends Action {

  value: string = '';

  constructor() {
    super();
    this.type = ActionType.Navigation;
  }
}

export class UrlAction extends Action {

  value: string = '';

  constructor() {
    super();
    this.type = ActionType.Url;
  }
}


export class Shape {
  actions: Action[] = [];
  static generateId() : string{
      return String.fromCharCode(65 + Math.floor(Math.random() * 26)) + Date.now();
  }

}

export class Page {

  id : string ;
  content: string = "";
  // transable name
  name: { [s: string]: string } = {};

  get displayName() :string {

    return Object.values(this.name)[0];
  }
  // transable description
  description: { [s: string]: string } = {};

  static generateId() : string{
    return String.fromCharCode(65 + Math.floor(Math.random() * 26)) + Date.now();
  }


  // string map of Shape
  shapes: { [s: string]: Shape } = {};

  static resolve(data:any):Page{
    return Object.assign(new Page() , data);
  }
}

export class Book {


  pages: Page[] = [];
  // transable name
  name: { [s: string]: string } = {};
  // transable description
  description: { [s: string]: string } = {};

  id : string ;


  get displayName():string {
    return Object.values(this.name).join(" / ");
  }
  get displayDescription() : string{
    return Object.values(this.description)[0];
  }


  static resolve(data:any) : Book{
    let book = Object.assign(new Book() , data);
    book.pages = book.pages.map( page => Page.resolve(page));
    return book ;
  }
}
