import {Component, OnInit} from '@angular/core';
import {Book} from 'src/app/models/book';
import {MatDialog} from '@angular/material';
import {AddBookDialogComponent} from 'src/app/site/dashboard/books-list/add-book-dialog/add-book-dialog.component';
import {BookService} from 'src/app/services/book.service';
import {ToastrService} from 'ngx-toastr';
import {AlertDialogComponent} from 'src/app/site/tools/alert-dialog/alert-dialog.component';
import {environment} from 'src/environments/environment';

@Component({
  selector: 'app-books-list',
  templateUrl: './books-list.component.html',
  styleUrls: ['./books-list.component.scss']
})
export class BooksListComponent implements OnInit {

  books: Book[];

  apiEndPoint = environment.apiEndPoint;
  columns: Book[][] = [[], [], [], []];

  columnIndex = 0;

  constructor(private dialog: MatDialog, private bookService: BookService
    , private toastr: ToastrService) {
  }

  addToColumn(book: Book) {

    this.columns[this.columnIndex++].push(book);
    this.columnIndex %= this.columns.length;

  }
  orderBooks(){

    for(let index in this.columns){
      this.columns[index] = [] ;
    }

    for (let book of this.books) {
      this.addToColumn(book);
    }
  }

  async ngOnInit() {
    this.books = await this.bookService.get().toPromise();
    this.orderBooks();
  }


  async addBook(book: Book) {
    try {

      let result = await this.bookService.post(book).toPromise();
      this.addToColumn(result);

      this.toastr.success('Book has been added successfully');

    } catch (e) {
      this.toastr.error('An error has been occurred please try againg later');
    }
  }

  async editBook(book: Book) {
    try {

      await this.bookService.patch({id: book.id, name: book.name, description: book.description}).toPromise();

      this.toastr.success('Book has been edited successfully');

    } catch (e) {
      this.toastr.error('An error has been occurred please try againg later');
    }
  }

  openAddDialog() {


    const dialogRef = this.dialog.open(AddBookDialogComponent, {
      width: '600px',
      data: new Book()

    });
    dialogRef.afterClosed().subscribe((result: Book) => {

      if (!result) {
        return;
      }
      this.addBook(result);

    });

  }

  edit(book: Book) {

    const dialogRef = this.dialog.open(AddBookDialogComponent, {
      width: '600px',
      data: book
    });
    dialogRef.afterClosed().subscribe(() => {
      this.editBook(book);
    });
  }

  delete(book:Book){
    const dialogRef = this.dialog.open( AlertDialogComponent , {
      width: '600px'
    });
    dialogRef.afterClosed().subscribe( async (res) => {

      if (res) {
        // delete book
        this.books = this.books.filter(b => b.id != book.id);
        this.orderBooks();

        // @todo BookView Model
        try {
          await this.bookService.delete(book).toPromise();
          this.toastr.success("book has been deleted successfully");
        } catch (e) {
          this.books.push(book);
          this.orderBooks();

          this.toastr.error("Something went wrong");
        } finally {
        }
      }
    });
  }

}
